import React, { useState } from 'react'

import { FormattedMessage } from 'react-intl';
import { useSelector } from 'react-redux';
import { IntlProvider } from 'react-intl';
import enLoacleData from '../../locales/en';
import zhLoacleData from '../../locales/zh-CN';
import { useNavigate } from 'react-router-dom';

import Head from '../../components/Head';

import cache from '../../static/cache.svg'
import server from '../../static/server.svg'

import './index.css'
import SocketElement from '../../components/SocketElement';

export default () => {
    const lang = useSelector((state) => state.lang.value);
    const messages = lang === 'en' ? enLoacleData : zhLoacleData;
    const go = useNavigate()

    const [index, setIndex] = useState(0)
    const [path, setPath] = useState('/cache')


    return (
        <IntlProvider locale={lang} messages={messages} key={lang}>
            <SocketElement />
            <div className='content'>
                <Head />
                <div className='network_box '>
                    <div className='advanced'>
                        <div className='advanced_options' onClick={() => {
                            setIndex(0)
                            setPath('/cache')
                        }}
                            onDoubleClick={() => {
                                go('/cache')
                            }}
                            style={{ backgroundColor: `${index === 0 ? '#dbeced' : 'unset'}` }}>
                            <img src={cache} />
                            <p><FormattedMessage id='ctc' /></p>
                        </div>
                        <div className='advanced_options' onClick={() => {
                            setIndex(1)
                            setPath('/server')
                        }}
                            onDoubleClick={() => {
                                go('/server')
                            }}
                            style={{ backgroundColor: `${index === 1 ? '#dbeced' : 'unset'}` }} >
                            <img src={server} />
                            <p><FormattedMessage id='msd' /></p>
                        </div>
                    </div>
                </div>
                <div className='btns'>
                    <div onClick={() => { go('/image-select') }}>
                        <FormattedMessage id='back' />
                    </div>
                    <div className='index_btn' onClick={() => {
                        go(path)
                    }}>
                        <FormattedMessage id='confirm' />
                    </div>
                </div>
            </div>
        </IntlProvider >
    )
}