import React, { useState, useEffect } from 'react'

import { FormattedMessage } from 'react-intl';
import { useSelector, useDispatch } from 'react-redux';
import { IntlProvider } from 'react-intl';
import enLoacleData from '../../locales/en';
import zhLoacleData from '../../locales/zh-CN';

import Progress from '../../components/Progress';
import mounting from '../../static/mounting.svg'
import { modeUpdata } from '../../utils/installSlice'

import result from '../../static/result-icon.svg'

import './index.css'

import { reboot, power_off, stopInstall } from '../../api'
import SocketElement from '../../components/SocketElement';

export default () => {
    const dispatch = useDispatch()
    const lang = useSelector((state) => state.lang.value);
    const messages = lang === 'en' ? enLoacleData : zhLoacleData;
    const [rebootTime, setrebootTome] = useState(null)

    const downMsg = useSelector((state) => state.install.value);

    useEffect(() => {
        if (downMsg) {
            if (downMsg.type === 'install') {
                if (downMsg.status === 'reboot') {
                    setrebootTome(downMsg.sec)
                } else if (downMsg.status === 'installed') {
                    setrebootTome(0)
                    dispatch(modeUpdata(null))
                    // reboot().then(() => {    
                    //     console.log('重启');
                    // }).catch(err => {

                    // })
                }
            }
        }
    }, [downMsg])

    return (
        <IntlProvider locale={lang} messages={messages} key={lang}>
            <SocketElement />
            <div className='content'>
                <Progress index='5' />
                <div className='network_box'>
                    <div className='setting_box'>
                        <div className='head_icon'>
                            <img src={mounting} className='pc_icon' />
                            <h2><FormattedMessage id='Successful installation' /></h2>
                        </div>
                        <div className='message'>
                            <img src={result} />
                            <h1><FormattedMessage id='Success' /></h1>
                            <p>About to reboot in {rebootTime} s</p>
                            <p className='abolish' onClick={() => {
                                stopInstall().then(res => {
                                    // 取消重启
                                    dispatch(modeUpdata(null))
                                }).catch(err => {
                                    console.log(err);
                                })
                            }}><FormattedMessage id='Cancel reboot' /></p>
                        </div>
                    </div>
                </div>
                <div className='btns'>
                    <div onClick={() => {
                        power_off().then(() => {
                            // 关机...
                        }).catch(err => {

                        })
                    }}>
                        <FormattedMessage id='turn_off'></FormattedMessage>
                    </div>
                    <div onClick={() => {
                        reboot().then(() => {
                            // 重启...

                        }).catch(err => {

                        })
                    }}>
                        <FormattedMessage id='reopen'></FormattedMessage>
                    </div>
                </div>
            </div>
        </IntlProvider >
    )
}