import React, { useEffect } from 'react'
import { useDispatch, } from 'react-redux';
import { modeUpdata } from '../../utils/installSlice'
import { CreateSocket, socket } from '../../api/websocket';

import { useNavigate } from 'react-router-dom';

export default () => {
    const dispatch = useDispatch()
    const go = useNavigate()

    useEffect(() => {
        const handleBeforeUnload = (event) => {
            if (socket !== null && !socket.closed) {
                socket.close();
            }
        };

        const handleUnload = (event) => {
            if (socket !== null && !socket.closed) {
                socket.close();
            }
        };



        CreateSocket();

        return () => {

        };
    }, [socket]);


    // 监听socket消息
    useEffect(() => {
        if (socket) {
            socket.on('respond', function (data) {
                let msg = data.type === 'install' || data.type === 'images'
                if (msg) {
                    if (window.location.pathname !== '/mounting' && window.location.pathname !== '/accreditation' && window.location.pathname !== '/success' && window.location.pathname !== '/medium') {
                        go('/mounting', { replace: true });
                    }
                } else if (data.type === 'authentication') {
                    if (data.status === 'success') {
                        localStorage.setItem('roobi_token', data.token)
                        if (localStorage.getItem('roobi_back_url')) {
                            go(localStorage.getItem('roobi_back_url'))
                        } else {
                            go('/networkTesting')
                        }
                    }
                }
                dispatch(modeUpdata(data))
            });
        } else {
        }



    }, [socket])


    return <div style={{ display: 'none' }}></div>
}