import React, { useEffect, useState } from 'react'

import { useSelector } from 'react-redux';


import { FormattedMessage } from 'react-intl';
import { IntlProvider } from 'react-intl';
import enLoacleData from '../../locales/en';
import zhLoacleData from '../../locales/zh-CN';

import Head from '../../components/Head';
import pc from '../../static/pcBox.svg'

import { Input, Select, message } from 'antd';
import { useNavigate } from 'react-router-dom';
import './index.css'

import { setNetworkdata, getNetworkInfo } from '../../api'
import SocketElement from '../../components/SocketElement';

const ipReg = /^(?:(?:25[0-5]|2[0-4]\d|[01]?\d{1,2})\.){3}(?:25[0-5]|2[0-4]\d|[01]?\d{1,2})\/([0-9]|[12][0-9]|3[0-2])$/


export default () => {
    const [messageApi] = message.useMessage();

    const lang = useSelector((state) => state.lang.value);
    const deviceType = useSelector((state) => state.device.value);
    const messages = lang === 'en' ? enLoacleData : zhLoacleData;
    const go = useNavigate()

    const [selectValue, setSelectValue] = useState('auto')
    const options = [
        { value: 'auto', label: 'Auto' },
        { value: 'shared', label: 'Shared' },
        { value: 'manual', label: 'Manual' },
        { value: 'disabled', label: 'Disabled' },
    ]

    const [warnings, setWarnings] = useState(false)
    const [ipAddress, setIpAddress] = useState('')
    const [gateway, setGateway] = useState('')
    const [dns, setDNS] = useState('')

    const [btn, setBtn] = useState(false)

    const selectChange = (v) => {
        setSelectValue(v)
        setIpAddress('')
        setGateway('')
        setDNS('')
    }

    useEffect(() => {
        if (selectValue === 'shared') {
            // warnings
            if (!warnings && ipAddress.length > 0) setBtn(true)
        } else if (selectValue === 'manual') {
            if (!warnings && gateway.length > 0 && ipAddress.length > 0) setBtn(true)
        } else if (selectValue === 'auto' || selectValue === 'disabled') {
            setBtn(true)
        }

    }, [selectValue, warnings, gateway])


    useEffect(() => {
        getNetworkInfo(deviceType).then(res => {
            if (res.data) {
                setSelectValue(res.data.method)
                setIpAddress(res.data.addresses)
                setGateway(res.data.gateway)
                setDNS(res.data.dns)
            } else {
                setSelectValue('auto')
            }
        }).catch(err => { })
    }, [])

    const setNetworkFn = () => {
        let data = {
            device: deviceType,
            method: selectValue,
            addresses: ipAddress,
            gateway: gateway,
            dns: dns,
        }
        setNetworkdata(data).then(res => {
            go('/configure', { replace: true })
        }).catch(err => console.error(err))
    }


    const addressElement = <>
        <span><FormattedMessage id='IP Address' /></span>
        <Input
            placeholder='IP / Subnet Mask'
            style={{ borderColor: `${warnings ? 'red' : '#4096ff'}` }}
            required={selectValue === 'shared' || selectValue === 'manual'}
            value={ipAddress} onChange={(v) => {
                setIpAddress(v.target.value)
            }} onBlur={(v) => {
                if (ipReg.test(v.target.value)) {
                    setWarnings(false)
                    setIpAddress(v.target.value)
                } else {
                    setWarnings(true)
                    messageApi.open({
                        type: 'error',
                        content: 'Please enter the ip address in the correct format',
                    });
                }
            }} className='input_text' />
    </>

    const gatewayElement = <>
        <span><FormattedMessage id='gateway' /></span>
        <Input
            required={selectValue === 'manual'}
            value={gateway} onChange={(v) => {
                setGateway(v.target.value)
            }} className='input_text' />
    </>

    const dnsElement = <>
        <span>DNS</span>
        <Input value={dns} onChange={(v) => {
            setDNS(v.target.value)
        }} className='input_text' />
    </>

    const dataArr = [addressElement, gatewayElement, dnsElement]

    return (
        <IntlProvider locale={lang} messages={messages} key={lang}>
            <SocketElement />
            <div className='content'>
                <Head />
                <div className='network_box'>
                    <div className='setting_box'>
                        <div className='head_icon'>
                            <img src={pc} className='pc_icon' />
                            <h2><FormattedMessage id='Select Media' /></h2>
                        </div>
                        <ul className='inputs'>
                            <li>
                                <span><FormattedMessage id='method' /></span>
                                <Select
                                    className='input_text'
                                    value={selectValue}
                                    onChange={selectChange}
                                    options={options}
                                />
                            </li>
                            {
                                dataArr.map((item, key) => {
                                    if (selectValue === 'auto' || selectValue === 'disabled') {
                                        return null

                                    } else if (selectValue === 'shared') {
                                        if (key == 0) {
                                            return <li key={key}>{item}</li>
                                        }
                                    } else if (selectValue === 'gateway') {
                                        if (key != 0) {
                                            return <li key={key}>{item}</li>
                                        }
                                    } else {
                                        return <li key={key}>{item}</li>
                                    }
                                })

                            }
                        </ul>
                    </div>
                </div>
                <div className='btns'>
                    <div onClick={() => { go('/configure') }}><FormattedMessage id='cancel' /></div>
                    <div onClick={btn ? setNetworkFn : null}><FormattedMessage id='confirm' /></div>
                </div>
            </div>
        </IntlProvider >
    )
}