import { io } from 'socket.io-client';

export let socket = null;
const namespace = '/websocket';
// const websocketUrl = 'http://192.168.2.70:739/websocket'
// const websocketUrl = 'http://192.168.2.184:739/websocket'
// const websocketUrl = window.location.protocol + '//' + '192.168.2.133:739' + namespace
const websocketUrl = window.location.protocol + '//' + window.location.host + namespace

export function CreateSocket() {
    var skid = '';

    if (socket !== null) return;

    socket = io.connect(websocketUrl);

    socket.on('connect', function (data) {
        skid = socket.id;
        localStorage.setItem('roobi_skid', skid);
    })

    // 建立断开事件
    socket.on('disconnect', function (data) {
    });

    // 监听事件
    socket.on('respond', function (data) {

    });

    return skid
}