import React, { useEffect, useState } from 'react'
import { BrowserRouter, Route, Routes } from 'react-router-dom';

import Home from './home';
import Protocols from './protocols';
import NetworkTesting from './networkTesting';
import Configure from './configure';
import Setting from './setting';
import Mirroring from './image-select';
import Medium from './medium';
import Advanced from './advanced';
import Cache from './cache';
import Media from './media';
import Mounting from './mounting';
import Accreditation from './accreditation';
import Success from './success';
import Failure from './failure';
import Server from './server';


import { getVersion, getMsg } from '../api'


export default () => {
    const [msg, setMsg] = useState(null)

    useEffect(() => {
        getVersion().then(res => {
            localStorage.setItem('roobi_vision', res.data.version)
        }).catch(err => {
            localStorage.setItem('roobi_vision', 'null')
        })
    }, [window.location.pathname])

    useEffect(() => {
        getMsg().then(res => {
            setMsg(res.data)
            if (window.location.pathname !== '/mounting' && window.location.pathname !== '/accreditation' && window.location.pathname !== '/failure' && window.location.pathname !== '/medium') {
                if (res.data.status !== 'stop' && res.data.status !== 'installed') {
                    window.location.href = '/mounting'
                }
            }
        })
    }, [])




    const routerData = [
        // 首页选择语言
        {
            path: '/',
            element: <Home />,
        },
        // 协议
        {
            path: '/protocols',
            element: <Protocols />,
        },
        // 认证
        {
            path: '/accreditation',
            element: <Accreditation />,
        },
        // 检查网络
        {
            path: '/networkTesting',
            element: <NetworkTesting />,
        },
        // 网络配置
        {
            path: '/configure',
            element: <Configure />,
        },
        // 设置
        {
            path: '/setting',
            element: <Setting />,
        },
        // 镜像列表
        {
            path: '/image-select',
            element: <Mirroring />,
        },
        // 媒介
        {
            path: '/medium',
            element: <Medium />,
        },
        // 高级设置
        {
            path: '/advanced',
            element: <Advanced />,
        },
        // 清除缓存
        {
            path: '/cache',
            element: <Cache />,
        },
        // 修改服务器
        {
            path: '/media',
            element: <Media />,
        },
        // 安装
        {
            path: '/mounting',
            element: <Mounting pMsg={msg} />,
        },
        // 安装成功
        {
            path: '/success',
            element: <Success />,
        },
        // 安装失败
        {
            path: '/failure',
            element: <Failure />,
        },
        // 设置服务器地址
        {
            path: '/server',
            element: <Server />,
        },
    ]


    return (
        <>
            <BrowserRouter>
                <Routes>
                    {
                        routerData.map((item, key) => {
                            return (
                                <Route key={key} path={item.path} element={item.element} />
                            )
                        })
                    }
                </Routes>
            </BrowserRouter>
        </>
    )

}