import React from 'react'
import { FormattedMessage } from 'react-intl';
import './index.css'

import logo from '../../static/logo.svg'
import res from '../../static/res.svg'
import res_c from '../../static/res-c.svg'

export default (props) => {
    const { index } = props

    return (
        <>
            <div className='progress_box'>
                <div className='logo_head'>
                    <img src={logo} className='logo' />
                    <p className='releases'><FormattedMessage id='version' /> {localStorage.getItem('roobi_vision') || ''}</p>
                </div>
                <ul className='progress_ul'>
                    <li className='last_li'>
                        <div className='img_box'>
                            <img src={index >= 1 ? res : res_c} />
                        </div>
                        <div className='progress_line' style={{ backgroundColor: `${index >= 1 ? '#fff' : '#d1d1d1'}` }}></div>
                        <p><FormattedMessage id='network'></FormattedMessage></p>
                    </li>
                    <li>
                        <div className='img_box'>
                            <img src={index >= 2 ? res : res_c} />
                        </div>
                        <div className='progress_line' style={{ backgroundColor: `${index >= 2 ? '#fff' : '#d1d1d1'}` }}></div>
                        <p><FormattedMessage id='configure'></FormattedMessage></p>
                    </li>
                    <li>
                        <div className='img_box'>
                            <img src={index >= 3 ? res : res_c} />
                        </div>
                        <div className='progress_line' style={{ backgroundColor: `${index >= 3 ? '#fff' : '#d1d1d1'}` }}></div>
                        <p><FormattedMessage id='image'></FormattedMessage></p>
                    </li>
                    <li>
                        <div className='img_box'>
                            <img src={index >= 4 ? res : res_c} />
                        </div>
                        <div className='progress_line' style={{ backgroundColor: `${index >= 4 ? '#fff' : '#d1d1d1'}` }}></div>
                        <p><FormattedMessage id='media'></FormattedMessage></p>
                    </li>
                    <li className='fast_li'>
                        <div className='img_box'>
                            <img src={index >= 5 ? res : res_c} />
                        </div>
                        <div className='progress_line' style={{ backgroundColor: `${index >= 5 ? '#fff' : '#d1d1d1'}` }}></div>
                        <p><FormattedMessage id='completed'></FormattedMessage></p>
                    </li>
                </ul>
            </div>
        </>
    )


}