import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';

import { FormattedMessage } from 'react-intl';
import { useSelector } from 'react-redux';
import { IntlProvider } from 'react-intl';
import enLoacleData from '../../locales/en';
import zhLoacleData from '../../locales/zh-CN';

import Head from '../../components/Head';

import cache from '../../static/cache.svg'
import warnings from '../../static/warnings.svg'
import result from '../../static/result-icon.svg'

import { clearCache } from '../../api'

import './index.css'
import SocketElement from '../../components/SocketElement';

export default () => {
    const lang = useSelector((state) => state.lang.value);
    const messages = lang === 'en' ? enLoacleData : zhLoacleData;
    const go = useNavigate()

    const [loading, setLoading] = useState(false)
    const [type, setType] = useState(false)
    const [dis, setDis] = useState(false)

    const clearFn = () => {
        setLoading(true)
        clearCache().then(res => {
            if (res.data.msg === 'success') {
                setLoading(false)
                setType(true)
                setDis(true)
                setTimeout(() => {
                    go('/advanced', { replace: true })
                }, 1000)
            }
        }).catch(err => {
            setLoading(false)
            setType(false)
        })
    }

    const styles = {
        backgroundColor: '#959595',
        borderColor: '#959595',
        cursor: 'not-allowed',
        color: '#fff',
    }

    return (
        <IntlProvider locale={lang} messages={messages} key={lang}>
            <SocketElement />
            <div className='content'>
                <Head />
                <div className='network_box'>
                    <div className='setting_box'>
                        <div className='head_icon'>
                            <img src={cache} className='pc_icon' />
                            <h2><FormattedMessage id='ctc'></FormattedMessage></h2>
                        </div>
                        <div className='warnings'>
                            {
                                loading ? <div className='loadingAnimation' style={{ display: `${loading ? 'flex' : 'none'}` }}>
                                    <div className='animation_ele'></div>
                                </div> : <>
                                    <img src={type ? result : warnings} />
                                    <p>{type ? <FormattedMessage id='cleared'></FormattedMessage> : <FormattedMessage id='error'></FormattedMessage>}</p>
                                </>
                            }
                        </div>
                    </div>
                </div>
                <div className='btns'>
                    <div onClick={() => { go('/advanced', { replace: true }) }}><FormattedMessage id='prev'></FormattedMessage></div>
                    <div
                        onClick={loading && dis ? null : clearFn}
                        style={dis ? styles : null}
                    ><FormattedMessage id='confirm'></FormattedMessage></div>
                </div>
            </div>
        </IntlProvider >
    )




}