import { createSlice } from '@reduxjs/toolkit'

export const downSlice = createSlice({
    name: 'down',
    initialState: {
        value: JSON.parse(localStorage.getItem('roobi_down') || null) || '',
    },
    reducers: {
        downData: (state, action) => {
            state.value = action.payload
            localStorage.setItem('roobi_down', JSON.stringify(action.payload));
        },
    },
})

export const { downData } = downSlice.actions

export default downSlice.reducer