import axios from "axios";
import { message } from 'antd'

const CancelToken = axios.CancelToken;

let cancel;

const serverConfig = {
    // baseURL: 'http://192.168.2.70:739',
    // baseURL: 'http://192.168.2.133:739',
    baseURL: '/',
    // 镜像列表
    // baseURL: 'http://192.168.2.54:739',
    useTokenAuthorization: true // 是否开启 token 认证
}


const axiosServer = axios.create({
    baseURL: serverConfig.baseURL, // 基础请求地址
    timeout: 30000, // 请求超时设置
    withCredentials: false // 跨域请求是否需要携带 cookie
})


// 请求拦截
axiosServer.interceptors.request.use(
    (config) => {
        if (serverConfig.useTokenAuthorization) {
            config.headers.Authorization = localStorage.getItem('roobi_token')

        }

        return config
    },
    (error) => {
        console.error(error);
        return Promise.reject(error)
    }
)
var hasExecuted401 = false;

// 响应拦截
axiosServer.interceptors.response.use(
    (res) => {
        return res
    },
    (error) => {
        let errorCode = error.request.status || 500

        if (errorCode === 401 && !hasExecuted401) {
            message.error('Authentication timeout', [2], () => {
                localStorage.setItem('roobi_back_url', window.location.pathname);
                window.location.href = "/accreditation"
            });
            hasExecuted401 = true;
        }
        return Promise.reject(error)
    }
)

// 修改wifi状态
const setWifiOption = (wifiType) => {

    return axiosServer.post('/network/wifi_options/', wifiType)
}

// 获取wifi状态
const getWifiOption = () => {
    if (cancel) {
        cancel();
    }
    return axiosServer.get('/network/wifi_options/', {
        cancelToken: new CancelToken(function executor(c) {
            // 保存 cancel 函数供后续调用
            cancel = c;
        })
    })
}

