import { createSlice } from '@reduxjs/toolkit'
export const installSlice = createSlice({
    name: 'install',
    initialState: {
        value: JSON.parse(localStorage.getItem('roobi_install') || null) || null,
    },
    reducers: {
        modeUpdata: (state, action) => {
            state.value = action.payload
            localStorage.setItem('roobi_install', JSON.stringify(action.payload));
        },
    },
})

export const { modeUpdata } = installSlice.actions

export default installSlice.reducer