import { createSlice } from '@reduxjs/toolkit'

export const deviceSlice = createSlice({
    name: 'device',
    initialState: {
        value: localStorage.getItem('roobi_device') || '',
    },
    reducers: {
        deviceType: (state, action) => {
            state.value = action.payload
            localStorage.setItem('roobi_device', action.payload);
        },
    },
})

export const { deviceType } = deviceSlice.actions

export default deviceSlice.reducer