import React, { useEffect, useState } from 'react'
import { FormattedMessage } from 'react-intl';
import { useSelector } from 'react-redux';
import { IntlProvider } from 'react-intl';
import enLoacleData from '../../locales/en';
import zhLoacleData from '../../locales/zh-CN';
import { useNavigate } from 'react-router-dom';

import Head from '../../components/Head';
import pc from '../../static/pcBox.svg'
import { Input } from 'antd';
import { setServer, getServer } from '../../api'
import SocketElement from '../../components/SocketElement';


export default () => {
    const lang = useSelector((state) => state.lang.value);
    const messages = lang === 'en' ? enLoacleData : zhLoacleData;
    const go = useNavigate()
    const [value, setValue] = useState('')

    useEffect(() => {
        getServer().then(res => {
            console.log(res.data.server);
            if (res.data.server) {
                setValue(res.data.server)
            } else {
                setValue('')
            }
        }).catch(err => { })
    }, [])


    return (
        <IntlProvider locale={lang} messages={messages} key={lang}>
            <SocketElement />
            <div className='content'>
                <Head />
                <div className='network_box'>
                    <div className='setting_box'>
                        <div className='head_icon'>
                            <img src={pc} className='pc_icon' />
                            <h2><FormattedMessage id='Set server address' /></h2>
                        </div>
                        <ul className='inputs'>
                            <li style={{ margin: 0 }}>
                                <span><FormattedMessage id='server address' /></span>
                                <Input placeholder="" className='input_text' value={value} onChange={(e) => { setValue(e.target.value) }} />
                            </li>
                        </ul>
                        <p style={{ textAlign: 'center', color: 'red' }}><FormattedMessage id='wcd' /></p>
                    </div>
                </div>
                <div className='btns'>
                    <div onClick={() => {
                        go('/advanced', { replace: true })
                    }}> <FormattedMessage id='cancel' /></div>
                    <div
                        className='index_btn'
                        onClick={() => {
                            // 修改服务器地址
                            setServer({ server: value }).then(res => {
                                go('/advanced', { replace: true })
                            }).catch(err => {
                                console.log(err);
                            })
                        }}><FormattedMessage id='confirm' /></div>
                </div>
            </div>
        </IntlProvider >
    )




}