import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { Select, ConfigProvider } from 'antd';
import { FormattedMessage, IntlProvider } from 'react-intl';
import enLoacleData from '../../locales/en';
import zhLoacleData from '../../locales/zh-CN';

import langimg from '../../static/lang-icon.svg'
import { increment } from '../../utils/langSlice'

import './index.css'
import { forTest } from '../../api'
import SocketElement from '../../components/SocketElement';

export default () => {
    const lang = useSelector((state) => {
        return state.lang.value
    })
    const messages = lang === 'en' ? enLoacleData : zhLoacleData;
    const dispatch = useDispatch()

    const handleChange = (value) => {
        dispatch(increment(value.value))
    };

    const [clickCount, setClickCount] = useState(0);

    useEffect(() => {
        const handleClick = () => {
            setClickCount(prevCount => prevCount + 1);
        };

        if (clickCount >= 5) {
            // 执行对应的逻辑
            setClickCount(0);
            forTest().then(res => {
            }).catch(err => {
            })
        }
        const testEle = document.querySelector('.testEle');
        if (testEle) {
            testEle.addEventListener('click', handleClick);
            return () => {
                testEle.removeEventListener('click', handleClick);
            };
        }

    }, [clickCount]);

    return (
        <IntlProvider locale={lang} messages={messages} key={lang}>
            <ConfigProvider >
                <SocketElement />
                <div className='content'>
                    <img src={langimg} className='testEle' />
                    <h1><FormattedMessage id='language'></FormattedMessage></h1>
                    <p><FormattedMessage id='select_language'></FormattedMessage></p>
                    <Select
                        labelInValue
                        defaultValue={{
                            value: lang,
                            label: lang == 'en' ? 'English' : '中文(简体)',
                        }}
                        style={{
                            width: 120,
                        }}
                        onChange={handleChange}
                        className='home_select'
                        options={[
                            {
                                value: 'en',
                                label: 'English',
                            },
                            {
                                value: 'zh-CN',
                                label: '中文(简体)',
                            },
                        ]}
                    />
                    <a href='/protocols' className='next'><FormattedMessage id='start'></FormattedMessage></a>
                </div>
            </ConfigProvider>
        </IntlProvider >
    )

}