import { createSlice } from '@reduxjs/toolkit'
export const networkSlice = createSlice({
    name: 'networkData',
    initialState: {
        value: JSON.parse(localStorage.getItem('roobi_networkData') || null) || [],
    },
    reducers: {
        changeNetworkData: (state, action) => {
            state.value = action.payload
            localStorage.setItem('roobi_networkData', JSON.stringify(action.payload));
        },
    },
})

export const { changeNetworkData } = networkSlice.actions

export default networkSlice.reducer