import React, { useEffect } from 'react'
import { FormattedMessage } from 'react-intl';
import { useSelector, useDispatch } from 'react-redux';
import { IntlProvider } from 'react-intl';
import { useNavigate } from 'react-router-dom';

import enLoacleData from '../../locales/en';
import zhLoacleData from '../../locales/zh-CN';
import Progress from '../../components/Progress';

import mounting from '../../static/mounting.svg'
import closeErr from '../../static/close-circle-fill.svg'

import { reboot, power_off } from '../../api'
import { modeUpdata } from '../../utils/installSlice'
import { addImage } from '../../utils/imageSlice'
import SocketElement from '../../components/SocketElement';

export default () => {
    const lang = useSelector((state) => state.lang.value);
    const messages = lang === 'en' ? enLoacleData : zhLoacleData;
    const downMsg = useSelector((state) => state.install.value);
    const go = useNavigate()
    const dispatch = useDispatch()

    useEffect(() => {
        if (!downMsg) {
            go('/image-select')
        }
    }, [])

    const failureType = localStorage.getItem('rb_cancel')

    let type1 = <> <FormattedMessage id='failure' /><br></br>{downMsg.msg || <FormattedMessage id='failure' />}</>
    let type2 = <><FormattedMessage id='User has cancelled'></FormattedMessage></>

    return (
        <IntlProvider locale={lang} messages={messages} key={lang}>
            <SocketElement />
            <div className='content'>
                <Progress index='' />
                <div className='network_box'>
                    <div className='setting_box'>
                        <div className='head_icon'>
                            <img src={mounting} className='pc_icon' />
                            <h2><FormattedMessage id='failure' /></h2>
                        </div>
                        <div className='message'>
                            <img src={closeErr} />
                            <h1>
                                {failureType ? type2 : type1}
                            </h1>
                        </div>
                    </div>
                </div>
                <div className='btns'>
                    <div onClick={() => {
                        power_off().then(() => {
                            // 关机...

                        }).catch(err => {

                        })
                    }}>
                        <FormattedMessage id='turn_off'></FormattedMessage>
                    </div>
                    <div onClick={() => {
                        reboot().then(() => {
                            // 重启...

                        }).catch(err => {

                        })
                    }}>
                        <FormattedMessage id='reopen'></FormattedMessage>
                    </div>
                    <div
                        className='index_btn'
                        onClick={() => {
                            dispatch(modeUpdata(null))
                            dispatch(addImage(null))
                            localStorage.removeItem('rb_cancel');
                            go('/image-select', { replace: true })
                        }}>
                        <FormattedMessage id='retry'></FormattedMessage>
                    </div>
                </div>
            </div>
        </IntlProvider >
    )
}