import { configureStore } from '@reduxjs/toolkit'
import langReducer from '../utils/langSlice'
import networkReducer from '../utils/networkSlice'
import deviceSlice from '../utils/deviceSlice'
import imageSlice from '../utils/imageSlice'
import downSlice from '../utils/downSlice'
import installSlice from '../utils/installSlice'

export default configureStore({
    reducer: {
        lang: langReducer,
        networkData: networkReducer,
        device: deviceSlice,
        image: imageSlice,
        down: downSlice,
        install: installSlice,
    },
})