import React, { useEffect, useState } from 'react';
import axios from 'axios';

import { FormattedMessage } from 'react-intl';
import { IntlProvider } from 'react-intl';
import enLoacleData from '../../locales/en';
import zhLoacleData from '../../locales/zh-CN';

import { useSelector, useDispatch } from 'react-redux';
import { addImage } from '../../utils/imageSlice'
import { modeUpdata } from '../../utils/installSlice'

import sizeConversions from '../../utils/methods/sizeConversions'


import Progress from '../../components/Progress';
import del from '../../static/del.svg';
import download from '../../static/download.svg';

import { useNavigate } from 'react-router-dom';
import './index.css'

import { getRemoteList, getLocalList, getRemoteInfo, getLocalInfo, delLocalInfo, getfreeSpace } from '../../api'

import { ConfigProvider, Modal, message } from 'antd';
import SocketElement from '../../components/SocketElement';

export default () => {
    const [messageApi, contextHolder] = message.useMessage();

    const [index, setIndex] = useState(0)
    const lang = useSelector((state) => state.lang.value);
    const messages = lang === 'en' ? enLoacleData : zhLoacleData;
    const imageData = useSelector((state) => state.image.value);
    const go = useNavigate()
    const dispatch = useDispatch()


    const [mirroringList, setMirroringList] = useState(null)
    const [loading, setLoading] = useState(false)
    const [infoLoading, setInfoLoading] = useState(false)
    const [mapElement, setMapElement] = useState(null)
    const [mirroringinfo, setMirroringinfo] = useState(null)

    const [delele, setDelele] = useState(true)
    const [uuid, setUuid] = useState(null)
    const [isModalOpen, setIsModalOpen] = useState(false)
    const [isModalOpen2, setIsModalOpen2] = useState(false)
    const [deleteName, setDeleteName] = useState(null)
    const [failed, setFailed] = useState(false)

    const [infoFlags, setInfoFlags] = useState(false)

    const [freespace, setFreespace] = useState(null)
    const [size, setsize] = useState(null)
    const [isModalOpen3, setIsModalOpen3] = useState(false)


    useEffect(() => {
        getfreeSpace().then(res => {
            if (res.data.free > 0) {
                setFreespace(res.data.free)
            } else {
                setFreespace(0)
            }
        })
    }, [])



    const showModal = (data) => {
        setDeleteName(<>{data.name}&nbsp;&nbsp;<FormattedMessage id='version' />: {data.version}</>)
        setUuid(data.uuid)
        setIsModalOpen(true);
    };
    // 删除本地镜像
    const delFn = () => {
        delLocalInfo(uuid).then(res => {
            setIsModalOpen(false);
            setMirroringinfo(null)
            dispatch(modeUpdata(null))
            setDelele(f => !f)
            setIndex(0)
        }).catch(err => {
        })
    }

    const handleCancel = () => {
        setIsModalOpen(false);
    };

    // 获取镜像列表
    useEffect(() => {
        setLoading(false)
        Promise.all([getRemoteList(), getLocalList()])
            .then(([remoteRes, localRes]) => {
                const newLocalData = localRes.data.map(item => {
                    let data = item
                    data.type = 'local'
                    return data
                })

                // 去重留下local
                const newData = [...remoteRes.data, ...newLocalData].reduce((result, current) => {
                    const existingIndex = result.findIndex(obj => obj.uuid === current.uuid);
                    if (existingIndex !== -1) {
                        result.splice(existingIndex, 1);
                    }
                    result.push(current);
                    return result;
                }, []);

                setFailed(true)
                setMirroringList(newData);
                setLoading(true)
            }).catch((error) => {
                if (error.request) {
                    if (error.request.status === 500) {
                        if (error.config.url === '/images/local/list/') {
                            getRemoteList().then(res => {
                                setMirroringList(res.data);
                                setLoading(true)
                            })
                        } else if (error.config.url === '/images/remote/list/') {
                            getLocalList().then(res => {
                                const newLocalData = res.data.map(item => {
                                    let data = item
                                    data.type = 'local'
                                    return data
                                })
                                setFailed(true)
                                setMirroringList(newLocalData);
                                setLoading(true)
                            })
                        } else {
                            setLoading(true)
                        }
                    }
                }
            })

    }, [delele])

    // 加载
    useEffect(() => {
        if (loading) {
            setMapElement(mirroringList.map((item, key) => {
                return <p
                    onClick={() => {
                        setIndex(key)
                    }}
                    className={index === key ? 'color329fa7' : null}
                    key={key}>
                    {item.name}
                    {
                        item.type === 'local' ? <img src={download} /> : null
                    }
                </p>
            }))
        } else {
            setMapElement(<div className='loadingAnimation' style={{ display: `${loading ? 'none' : 'flex'}` }}>
                <h2>{failed ? <FormattedMessage id='loading err' /> : <FormattedMessage id='loading' />}</h2>
                {
                    failed ? null : <div className='animation_ele'></div>
                }
            </div>)
        }
    }, [loading, index])

    // 渲染
    useEffect(() => {
        setFailed(false)
        setInfoLoading(false)
        setMirroringinfo(<div className='loadingAnimation'>
            <h2><FormattedMessage id='loading' /></h2>
            <div className='animation_ele'></div>
        </div>)
        if (loading) {
            if (mirroringList.length > 0) {
                if (mirroringList[index].type === 'local') {

                    // 本地镜像
                    getLocalInfo(mirroringList[index].uuid).then(res => {

                        dispatch(addImage(res.data))
                        // size累加转换
                        let size = res.data.download.reduce((a, b) => {
                            return a + b.size
                        }, 0)
                        const sizzeSum = sizeConversions(size)
                        setsize(size)
                        setFailed(true)

                        let ele = <>
                            <div className='icons_box_2'>
                                <img src={res.data.img} className='os_icon' />
                                <div className='right_icons'>
                                    <img src={del} className='del_icon' onClick={() => showModal(res.data)} />
                                </div>
                            </div>
                            <h1><span>{res.data.name}</span></h1>
                            <p><FormattedMessage id='version' />: <span>{res.data.version}</span></p>
                            <p><FormattedMessage id='date' />: <span>{res.data.date}</span></p>
                            <p><FormattedMessage id='author' />: <span>{res.data.author}</span></p>
                            <p><FormattedMessage id='size' />: <span>{sizzeSum}</span></p>
                            <div className='p_info'>
                                <p style={{ whiteSpac: 'nowrap' }}><FormattedMessage id='description' />:</p>
                                <p>{res.data.description[lang || 'en']}</p>
                            </div>
                        </>
                        setInfoLoading(true)

                        setMirroringinfo(ele)
                    }).catch(err => {
                        let ele = <div className='icons_box' style={{ width: '100%', height: '100%' }} onClick={() => { setInfoFlags(f => !f) }}>
                            <div className='icon_box'>
                                <p><FormattedMessage id='Click on this area to retry' /></p>
                            </div>
                        </div>
                        setInfoLoading(true)
                        setMirroringinfo(ele)
                    })

                } else {
                    // 远程镜像详情
                    getRemoteInfo(mirroringList[index].url).then(res => {
                        dispatch(addImage(res.data))
                        let size = res.data.download.reduce((a, b) => {
                            return a + b.size
                        }, 0)
                        setsize(size)
                        // size累加转换
                        const sizzeSum = sizeConversions(size)
                        setFailed(true)

                        let ele = <>
                            <div className='icons_box_2'>
                                <img src={res.data.img} className='os_icon' />
                            </div>
                            <h1><span>{res.data.name}</span></h1>
                            <p><FormattedMessage id='version' />: <span>{res.data.version}</span></p>
                            <p><FormattedMessage id='date' />: <span>{res.data.date}</span></p>
                            <p><FormattedMessage id='author' />: <span>{res.data.author}</span></p>
                            <p><FormattedMessage id='size' />: <span>{sizzeSum}</span></p>
                            <div className='p_info'>
                                <p style={{ whiteSpace: 'nowrap' }}><FormattedMessage id='description' />:</p>
                                <p>{res.data.description[lang || 'en']}</p>
                            </div>
                        </>

                        setMirroringinfo(ele)
                        setInfoLoading(true)

                    }).catch(err => {
                        let ele = <div className='icons_box' style={{ width: '100%', height: '100%' }} onClick={() => { setInfoFlags(f => !f) }}>
                            <div className='icon_box'>
                                <p><FormattedMessage id='Click on this area to retry' /></p>
                            </div>
                        </div>
                        if (err.request) {
                            setMirroringinfo(ele)
                        }
                        setInfoLoading(true)
                    })
                }

            } else {
                setMapElement(
                    <div className='icon_box'>
                        <svg t="1696933746942" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="7266" width="70" height="70"><path d="M985.6 1017.6c-6.4 0-19.2-6.4-25.6-6.4l-160-160-153.6 160c-6.4 6.4-12.8 12.8-25.6 12.8-6.4 0-19.2-6.4-25.6-12.8-12.8-12.8-12.8-32 0-44.8l160-160L595.2 640c-6.4-12.8-6.4-32 0-44.8 6.4-6.4 12.8-12.8 25.6-12.8s12.8 6.4 19.2 12.8l166.4 166.4 160-166.4c6.4-6.4 12.8-12.8 25.6-12.8 6.4 0 19.2 6.4 25.6 6.4 6.4 12.8 6.4 32 0 44.8l-166.4 166.4 160 153.6c12.8 12.8 12.8 32 0 44.8-6.4 19.2-19.2 19.2-25.6 19.2zM403.2 620.8H217.6c-19.2 0-38.4-19.2-38.4-38.4s19.2-38.4 38.4-38.4h179.2c19.2 0 38.4 19.2 38.4 38.4 6.4 19.2-12.8 38.4-32 38.4zM345.6 38.4v147.2c0 19.2-19.2 38.4-38.4 38.4s-38.4-19.2-38.4-38.4V38.4c6.4-19.2 19.2-38.4 44.8-38.4 19.2 0 32 19.2 32 38.4zM742.4 38.4v147.2c0 19.2-19.2 38.4-38.4 38.4s-38.4-19.2-38.4-38.4V38.4c0-19.2 12.8-38.4 38.4-38.4 19.2 0 38.4 19.2 38.4 38.4z" p-id="7267" fill="#0c929c"></path><path d="M1017.6 198.4c0-64-51.2-115.2-115.2-121.6H128c-64 0-128 51.2-128 121.6V896c0 70.4 57.6 128 128 128h384c19.2 0 38.4-19.2 38.4-38.4s-19.2-44.8-38.4-44.8H128c-25.6 0-44.8-19.2-44.8-44.8V441.6H1024V198.4zM76.8 364.8V198.4c0-25.6 19.2-44.8 44.8-44.8H896c19.2 6.4 38.4 25.6 38.4 44.8v166.4H76.8z" p-id="7268" fill="#0c929c"></path><path d="M403.2 787.2H217.6c-19.2 0-38.4-19.2-38.4-38.4s19.2-38.4 38.4-38.4h179.2c19.2 0 38.4 19.2 38.4 38.4 6.4 19.2-12.8 38.4-32 38.4z" p-id="7269" fill="#0c929c"></path></svg>
                        <p>Failed to retrieve <br></br>the list of images</p>
                    </div>
                )
                setMirroringinfo(
                    <div className='icons_box'>
                        <div className='icon_box'>
                            <p>Failed to retrieve the details of the image</p>
                        </div>
                    </div>
                )
                setLoading(true)
                setInfoLoading(true)
            }

        }

    }, [loading, infoFlags, index])

    const nextFn = () => {
        if (infoLoading) {
            if (mirroringList[index].type !== 'local') {
                if (size > freespace) {
                    setIsModalOpen3(true)
                    return
                }
            }
            if (imageData.trust) {
                go('/medium')
            } else {
                setIsModalOpen2(true)
            }
        }
    }

    const styles = {
        backgroundColor: '#959595',
        borderColor: '#959595',
        cursor: 'not-allowed',
        color: '#fff',
    }

    const trustFn = () => {
        go('/medium')
    }

    const trustFn2 = () => {
        setIsModalOpen3(false)
    }

    return (
        <IntlProvider locale={lang} messages={messages} key={lang}>
            <ConfigProvider theme={{
                token: {
                    colorPrimary: '#329fa7',
                },
            }}>
                <SocketElement />
                {contextHolder}
                <Modal title={deleteName} open={isModalOpen} onOk={delFn} onCancel={handleCancel} centered={true} okText={<FormattedMessage id='confirm' />} cancelText={<FormattedMessage id='cancel' />} okType="danger">
                    <h2><FormattedMessage id='ci' /></h2>
                </Modal>
                <Modal title={<FormattedMessage id='kt' />} open={isModalOpen2} onOk={trustFn} onCancel={() => { setIsModalOpen2(false) }} centered={true} okText={<FormattedMessage id='confirm' />} cancelText={<FormattedMessage id='cancel' />} okType="danger">
                    <h2><FormattedMessage id='kind tips image' /></h2>
                </Modal>
                <Modal title={<FormattedMessage id='kt' />} open={isModalOpen3} onOk={trustFn2} onCancel={() => { setIsModalOpen3(false) }} centered={true} okText={<FormattedMessage id='confirm' />} cancelText={<FormattedMessage id='cancel' />} okType="danger">
                    <h2><FormattedMessage id='freespace' /></h2>
                </Modal>
                <div className='content'>
                    <Progress index='3' />
                    <div className='network_box '>
                        <div className='mirroring_box'>
                            <div className='left_os'>
                                {
                                    mapElement
                                }
                            </div>
                            <div className='right_os_info'>
                                {/* <div className='loadingAnimation' style={{ display: `${infoLoading ? 'none' : 'flex'}` }}>
                                    <h2><FormattedMessage id='loading' /></h2>
                                    <div className='animation_ele'></div>
                                </div> */}
                                {
                                    mirroringinfo
                                }

                            </div>
                        </div>
                    </div>
                    <div className='btns'>
                        <div onClick={() => { go('/advanced') }}>
                            <svg t="1696823221973" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4005" width="24" height="24"><path d="M892.928 128q28.672 0 48.64 19.968t19.968 48.64l0 52.224q0 28.672-19.968 48.64t-48.64 19.968l-759.808 0q-28.672 0-48.64-19.968t-19.968-48.64l0-52.224q0-28.672 19.968-48.64t48.64-19.968l759.808 0zM892.928 448.512q28.672 0 48.64 19.968t19.968 48.64l0 52.224q0 28.672-19.968 48.64t-48.64 19.968l-759.808 0q-28.672 0-48.64-19.968t-19.968-48.64l0-52.224q0-28.672 19.968-48.64t48.64-19.968l759.808 0zM892.928 769.024q28.672 0 48.64 19.968t19.968 48.64l0 52.224q0 28.672-19.968 48.64t-48.64 19.968l-759.808 0q-28.672 0-48.64-19.968t-19.968-48.64l0-52.224q0-28.672 19.968-48.64t48.64-19.968l759.808 0z" p-id="4006" fill="#329fa7"></path>
                            </svg>
                            <FormattedMessage id='more' />
                        </div>
                        <div onClick={() => { go('/networkTesting') }}>
                            <svg width="19" height="15" viewBox="0 0 19 15" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M3.13143 6.43213C2.84819 6.43213 2.57655 6.54465 2.37627 6.74493C2.17599 6.94521 2.06348 7.21684 2.06348 7.50008C2.06348 7.78332 2.17599 8.05496 2.37627 8.25524C2.57655 8.45552 2.84819 8.56803 3.13143 8.56803L17.0148 8.56804C17.298 8.56804 17.5697 8.45552 17.77 8.25524C17.9702 8.05496 18.0828 7.78332 18.0828 7.50008C18.0828 7.21685 17.9702 6.94521 17.77 6.74493C17.5697 6.54465 17.298 6.43213 17.0148 6.43213L3.13143 6.43213Z" fill="#329FA7" />
                                <path d="M2.49514 7.5L7.54656 1.80034C7.64461 1.69628 7.72077 1.57358 7.77052 1.43953C7.82027 1.30549 7.84259 1.16281 7.83617 1.01997C7.82975 0.877136 7.79471 0.737042 7.73313 0.608C7.67154 0.478959 7.58468 0.363597 7.47768 0.268755C7.37068 0.173914 7.24572 0.101524 7.11022 0.0558799C6.97472 0.0102355 6.83143 -0.00773471 6.68886 0.00303506C6.54628 0.0138048 6.40732 0.0530953 6.28021 0.118576C6.1531 0.184057 6.04044 0.274396 5.9489 0.384234L0.268461 6.79195C0.0954939 6.98725 -1.26573e-06 7.23911 -1.31134e-06 7.5C-1.35696e-06 7.76089 0.0954937 8.01275 0.268461 8.20805L5.94997 14.6158C6.04151 14.7256 6.15417 14.8159 6.28128 14.8814C6.40838 14.9469 6.54734 14.9862 6.68992 14.997C6.8325 15.0077 6.97578 14.9898 7.11129 14.9441C7.24679 14.8985 7.37174 14.8261 7.47874 14.7312C7.58574 14.6364 7.67261 14.521 7.73419 14.392C7.79577 14.263 7.83081 14.1229 7.83724 13.98C7.84366 13.8372 7.82134 13.6945 7.77159 13.5605C7.72184 13.4264 7.64568 13.3037 7.54762 13.1997L2.49514 7.5Z" fill="#329FA7" />
                            </svg>
                            <FormattedMessage id='prev' />
                        </div>
                        <div onClick={failed ? nextFn : null}
                            style={!failed ? styles : null}
                            className='index_btn'
                        >
                            <FormattedMessage id='next' />
                            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M17.9506 12.5679C18.2338 12.5679 18.5055 12.4554 18.7058 12.2551C18.906 12.0548 19.0186 11.7832 19.0186 11.4999C19.0186 11.2167 18.906 10.945 18.7058 10.7448C18.5055 10.5445 18.2338 10.432 17.9506 10.432L4.06722 10.432C3.78398 10.432 3.51234 10.5445 3.31206 10.7448C3.11178 10.945 2.99927 11.2167 2.99927 11.4999C2.99927 11.7832 3.11178 12.0548 3.31206 12.2551C3.51234 12.4554 3.78398 12.5679 4.06722 12.5679L17.9506 12.5679Z" fill="white" />
                                <path d="M18.5869 11.5L13.5355 17.1997C13.4374 17.3037 13.3613 17.4264 13.3115 17.5605C13.2618 17.6945 13.2394 17.8372 13.2459 17.98C13.2523 18.1229 13.2873 18.263 13.3489 18.392C13.4105 18.521 13.4974 18.6364 13.6044 18.7312C13.7114 18.8261 13.8363 18.8985 13.9718 18.9441C14.1073 18.9898 14.2506 19.0077 14.3932 18.997C14.5358 18.9862 14.6747 18.9469 14.8018 18.8814C14.9289 18.8159 15.0416 18.7256 15.1331 18.6158L20.8136 12.2081C20.9865 12.0127 21.082 11.7609 21.082 11.5C21.082 11.2391 20.9865 10.9873 20.8136 10.7919L15.1321 4.38423C15.0405 4.27439 14.9279 4.18406 14.8008 4.11857C14.6736 4.05309 14.5347 4.0138 14.3921 4.00303C14.2495 3.99226 14.1062 4.01023 13.9707 4.05588C13.8352 4.10152 13.7103 4.17391 13.6033 4.26875C13.4963 4.36359 13.4094 4.47896 13.3478 4.608C13.2863 4.73704 13.2512 4.87713 13.2448 5.01997C13.2384 5.16281 13.2607 5.30548 13.3104 5.43953C13.3602 5.57358 13.4364 5.69627 13.5344 5.80034L18.5869 11.5Z" fill="white" />
                            </svg>
                        </div>
                    </div>
                </div>
            </ConfigProvider>
        </IntlProvider >
    )

}

