import React, { useEffect, useState } from 'react'
import { FormattedMessage } from 'react-intl';
import { useSelector, useDispatch } from 'react-redux';
import { IntlProvider } from 'react-intl';
import enLoacleData from '../../locales/en';
import zhLoacleData from '../../locales/zh-CN';
import { useNavigate } from 'react-router-dom';
import { Modal, Select, Spin } from 'antd';

import Progress from '../../components/Progress';
import pc from '../../static/pcBox.svg'

import { getDiskInfo, startInstall } from '../../api'
import sizeConversions from '../../utils/methods/sizeConversions'
import { downData } from '../../utils/downSlice'

import './index.css'
import { socket } from '../../api/websocket';
import SocketElement from '../../components/SocketElement';


export default () => {
    const go = useNavigate()
    const dispatch = useDispatch()

    const lang = useSelector((state) => state.lang.value);
    const imageData = useSelector((state) => state.image.value);
    const messages = lang === 'en' ? enLoacleData : zhLoacleData;

    const [sizeFlag, setSizeFlag] = useState(false)
    const [loading, setLoading] = useState(false)
    const [options, setOptions] = useState([])
    const [indexOptions, setIndexOptions] = useState(0)
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [disk, setDisk] = useState(null)
    const [devName, setDevName] = useState('')
    const [flag, setFlag] = useState(false)
    const [rotation, setRotation] = useState(0);
    const [downDataInfo, setDownDataInfo] = useState(null);
    const [socketMsg, setSocketMsg] = useState(false);

    useEffect(() => {
        getDiskInfo().then(res => {
            if (res.data.length > 0) {
                let optionData = res.data.map((item, key) => {
                    return { value: key, label: `${item.model || null},${sizeConversions(item.size)},${item.path}`, data: item }
                })
                if (optionData[0].data.size < imageData.scripts[0].size) {
                    setSizeFlag(false)
                } else {
                    setSizeFlag(true)
                }
                setOptions(optionData)
            } else {

                setOptions([])
            }
            setLoading(true)
        }).catch(err => console.error(err));
    }, [flag])


    const optionChangeFn = (value, data) => {
        // data.path
        // data.type
        // data.model
        setIndexOptions(value)
        if (data.data.size < imageData.scripts[0].size) {
            setSizeFlag(false)
        } else {
            setSizeFlag(true)
        }
    }

    const nextFn = () => {
        if (sizeFlag) {
            let data = {
                js: imageData,
                param: {
                    disk,
                    language: lang,
                }
            }
            setDownDataInfo(data)
            dispatch(downData(data))
            setIsModalOpen(true)
        }
    }

    useEffect(() => {
        if (options.length > 0) {
            setDisk(options[indexOptions].data.path)
            setDevName(`${options[indexOptions].data.model}, ${options[indexOptions].data.path}`)
        } else {

        }
    }, [options, indexOptions])


    const styles = {
        backgroundColor: '#959595',
        borderColor: '#959595',
        cursor: 'not-allowed',
        color: '#fff',
    }

    useEffect(() => {
        if (!imageData) {
            go('/image-select')
        }
    }, [])

    // 监听socket消息
    useEffect(() => {
        if (socket) {
            socket.on('respond', function (data) {
                if (data.percent) {
                    go('/mounting')
                }
            });
        }
    }, [])


    const delFn = () => {
        startInstall(downDataInfo).then(res => {
            console.log(res);
            if (res.data === 'success') {
                setSocketMsg(true)
            } else {

            }
        }).catch(err => {

        })

    }

    return (
        <IntlProvider locale={lang} messages={messages} key={lang}>
            <Modal title={<FormattedMessage id='kt' />} open={isModalOpen} onOk={delFn} onCancel={() => setIsModalOpen(false)} centered={true} okText={!socketMsg ? <FormattedMessage id='confirm' /> : <div className='loader_spin' />} cancelText={<FormattedMessage id='cancel' />} okType="danger">
                <h2 style={{ color: 'red' }}><FormattedMessage id='ssc' /></h2>
                <h3>{devName}</h3>
            </Modal>
            <SocketElement />
            <div className='content'>
                <Progress index='3' />
                <div className='network_box'>
                    <div className='setting_box'>
                        <div className='head_icon'>
                            <img src={pc} className='pc_icon' />
                            <h2><FormattedMessage id='sm' /></h2>
                        </div>
                        {
                            !loading ? <div className='loadingAnimation'>
                                <div className='animation_ele'></div>
                            </div> : <>
                                <ul className='inputs prompt_ul'>
                                    <li>
                                        <span><FormattedMessage id='medium' /></span>
                                        <Select
                                            className='input_text'
                                            onChange={optionChangeFn}
                                            options={options}
                                            value={options.length === 0 ? 'The medium is not detected' : indexOptions}
                                        />
                                        <svg onClick={(e) => {
                                            setRotation(r => r + 360);
                                            setFlag(f => !f)
                                        }} style={{ transform: `rotate(${rotation}deg)` }} t="1696736506230" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4873" width="40" height="40" ><path d="M960 416V192l-73.056 73.056a447.712 447.712 0 0 0-373.6-201.088C265.92 63.968 65.312 264.544 65.312 512S265.92 960.032 513.344 960.032a448.064 448.064 0 0 0 415.232-279.488 38.368 38.368 0 1 0-71.136-28.896 371.36 371.36 0 0 1-344.096 231.584C308.32 883.232 142.112 717.024 142.112 512S308.32 140.768 513.344 140.768c132.448 0 251.936 70.08 318.016 179.84L736 416h224z" p-id="4874" fill="#0c929c" ></path></svg>
                                    </li>
                                </ul>
                                {
                                    sizeFlag ? <>
                                        <p className='prompt'><FormattedMessage id='mp' />: <span>{disk}</span></p>
                                        <p className='prompt'><FormattedMessage id='rs' />: <span>{sizeConversions(imageData.scripts[0].size)}</span></p>

                                    </> : options.length > 0 ? <p className='prompt' style={{ color: 'red' }}><FormattedMessage id='ui' /></p> : null
                                }
                            </>
                        }
                    </div>
                </div>
                <div className='btns'>
                    <div onClick={() => { go('/image-select') }}>
                        <svg width="19" height="15" viewBox="0 0 19 15" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M3.13143 6.43213C2.84819 6.43213 2.57655 6.54465 2.37627 6.74493C2.17599 6.94521 2.06348 7.21684 2.06348 7.50008C2.06348 7.78332 2.17599 8.05496 2.37627 8.25524C2.57655 8.45552 2.84819 8.56803 3.13143 8.56803L17.0148 8.56804C17.298 8.56804 17.5697 8.45552 17.77 8.25524C17.9702 8.05496 18.0828 7.78332 18.0828 7.50008C18.0828 7.21685 17.9702 6.94521 17.77 6.74493C17.5697 6.54465 17.298 6.43213 17.0148 6.43213L3.13143 6.43213Z" fill="#329FA7" />
                            <path d="M2.49514 7.5L7.54656 1.80034C7.64461 1.69628 7.72077 1.57358 7.77052 1.43953C7.82027 1.30549 7.84259 1.16281 7.83617 1.01997C7.82975 0.877136 7.79471 0.737042 7.73313 0.608C7.67154 0.478959 7.58468 0.363597 7.47768 0.268755C7.37068 0.173914 7.24572 0.101524 7.11022 0.0558799C6.97472 0.0102355 6.83143 -0.00773471 6.68886 0.00303506C6.54628 0.0138048 6.40732 0.0530953 6.28021 0.118576C6.1531 0.184057 6.04044 0.274396 5.9489 0.384234L0.268461 6.79195C0.0954939 6.98725 -1.26573e-06 7.23911 -1.31134e-06 7.5C-1.35696e-06 7.76089 0.0954937 8.01275 0.268461 8.20805L5.94997 14.6158C6.04151 14.7256 6.15417 14.8159 6.28128 14.8814C6.40838 14.9469 6.54734 14.9862 6.68992 14.997C6.8325 15.0077 6.97578 14.9898 7.11129 14.9441C7.24679 14.8985 7.37174 14.8261 7.47874 14.7312C7.58574 14.6364 7.67261 14.521 7.73419 14.392C7.79577 14.263 7.83081 14.1229 7.83724 13.98C7.84366 13.8372 7.82134 13.6945 7.77159 13.5605C7.72184 13.4264 7.64568 13.3037 7.54762 13.1997L2.49514 7.5Z" fill="#329FA7" />
                        </svg>
                        <FormattedMessage id='prev' />
                    </div>
                    <div className='index_btn'
                        onClick={sizeFlag && options.length > 0 ? nextFn : null}
                        style={sizeFlag && options.length > 0 ? null : styles}
                    >
                        <FormattedMessage id='next' />
                        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M17.9506 12.5679C18.2338 12.5679 18.5055 12.4554 18.7058 12.2551C18.906 12.0548 19.0186 11.7832 19.0186 11.4999C19.0186 11.2167 18.906 10.945 18.7058 10.7448C18.5055 10.5445 18.2338 10.432 17.9506 10.432L4.06722 10.432C3.78398 10.432 3.51234 10.5445 3.31206 10.7448C3.11178 10.945 2.99927 11.2167 2.99927 11.4999C2.99927 11.7832 3.11178 12.0548 3.31206 12.2551C3.51234 12.4554 3.78398 12.5679 4.06722 12.5679L17.9506 12.5679Z" fill="white" />
                            <path d="M18.5869 11.5L13.5355 17.1997C13.4374 17.3037 13.3613 17.4264 13.3115 17.5605C13.2618 17.6945 13.2394 17.8372 13.2459 17.98C13.2523 18.1229 13.2873 18.263 13.3489 18.392C13.4105 18.521 13.4974 18.6364 13.6044 18.7312C13.7114 18.8261 13.8363 18.8985 13.9718 18.9441C14.1073 18.9898 14.2506 19.0077 14.3932 18.997C14.5358 18.9862 14.6747 18.9469 14.8018 18.8814C14.9289 18.8159 15.0416 18.7256 15.1331 18.6158L20.8136 12.2081C20.9865 12.0127 21.082 11.7609 21.082 11.5C21.082 11.2391 20.9865 10.9873 20.8136 10.7919L15.1321 4.38423C15.0405 4.27439 14.9279 4.18406 14.8008 4.11857C14.6736 4.05309 14.5347 4.0138 14.3921 4.00303C14.2495 3.99226 14.1062 4.01023 13.9707 4.05588C13.8352 4.10152 13.7103 4.17391 13.6033 4.26875C13.4963 4.36359 13.4094 4.47896 13.3478 4.608C13.2863 4.73704 13.2512 4.87713 13.2448 5.01997C13.2384 5.16281 13.2607 5.30548 13.3104 5.43953C13.3602 5.57358 13.4364 5.69627 13.5344 5.80034L18.5869 11.5Z" fill="white" />
                        </svg>
                    </div>
                </div>
            </div>
        </IntlProvider >
    )




}