import { createSlice } from '@reduxjs/toolkit'
export const langSlice = createSlice({
    name: 'counter',
    initialState: {
        value: localStorage.getItem('roobi_lang') || 'en',
    },
    reducers: {
        increment: (state, action) => {
            state.value = action.payload
            localStorage.setItem('roobi_lang', action.payload);
        },
    },
})

export const { increment, decrement, incrementByAmount } = langSlice.actions

export default langSlice.reducer